
.hotels {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
}
.hotels .hotel {
    width: 45%;
    padding: 10px;
    margin-bottom: 10px;
    border-color: black;
}

.hotels .hotel h5 {
    font-size: 1em;
    font-weight: bold;
}