@import url(https://fonts.googleapis.com/css?family=Merriweather&display=swap);
* {
    box-sizing: border-box;
    font-family: 'Merriweather', serif;
}
html {
    height:100%;
    max-height:100%;
}
body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    height: 100%;
    color:#282828;
}

#root {
    height:100%;
}

.main h1 {
    text-align: center;
    padding-top: 0.7em;
    padding-bottom:0.8em;
    font-size:5em;
    font-style: italic;
}
.menus {
    height: 100%;
    display: flex;
    justify-content: center;

}
.menus .menu {
    display: flex;
    justify-content: center;
    align-items: center;
    width:100%;
}  
.menus .menu a {
    display: flex;
    align-items: center;
    justify-content: center;
    width:20em;
    height: 20em;
    border-radius: 10em;
    color:#fbefe1;
    text-decoration: none;
    -webkit-transition: 1s ease-out;
    transition: 1s ease-out;

}

.menus .menu a:hover {
    -webkit-transform: scale(1.1, 1.1);
            transform: scale(1.1, 1.1);
    
}
.menus .menu.m1 a {
    background-color: #ca4751;
}
.menus .menu.m2 a {
    background-color: #4782ca;
}

.menus .menu a span {
    font-size: 2em;
    font-weight:bold;
}

.container {
    height: 100%;
    display: flex;
    justify-content: center;
}
.spotitems {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding:50px;

}
.spotitems .spotitem {
    display:flex;
    justify-content: center;
    width: 45%;
    padding: 10px;
    margin-bottom: 10px;
}
.spotitems .spotitem h5 {
    text-align: center;
    font-size: 1em;
    font-weight: bold;
}
.spotitems .spotitem img {
    width:300px;
    margin-bottom: 2em;
}
.spotdetails {
    display: flex;
    justify-content: center;
    padding-top:4em;
    height:100%;
}

.spotdetails .spotdetail {
    flex:1 1;
    flex-direction: column;
}
.spotdetails h3 {
    text-align: center;
}
.spotdetails img {
    display: flex;
    margin: auto;
}
.spotdetails a {
    text-align: center;
    display: block;
}

.hotels {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
}
.hotels .hotel {
    width: 45%;
    padding: 10px;
    margin-bottom: 10px;
    border-color: black;
}

.hotels .hotel h5 {
    font-size: 1em;
    font-weight: bold;
}
.hoteldetails {
    display: flex;
    justify-content: center;
    /* padding-top:4em; */
    height:100%;
    flex-direction: column;
}
.hoteldetails h5,
.hoteldetails span {
    text-align: center;
}

.hoteldetails span{
    padding-top:0.5em;
}
.navbar {
    background: rgba(255,255,255,0.4);
}
.navbar a {
    margin-right:2em;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fbefe1;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a:link,
a:visited,
a:hover {
    color:#282828;
    text-decoration: none;
}
